import { defineStore } from "pinia";
import DataService from "../services/DataService";
import { useloggedUserInfo } from "./UserInfo"
import {  formatter,ShowAlert } from "@/assets/common/vue-common.js"
import i18n from '../localization/index'

export const userTimerInfo = defineStore("TimeTracker", {
    state: () => ({
        timerCountInSec: 0,
        breakTimerCountInSec:0,
        timerInfo: '',
        timerProjectDetails: "",
        projects: [],
        tasks: [],
        breakTimerList: [],
        loading: false,
        timerListing: [],
        timerRunResponse:'',
        timerClock:"00:00:00",
        breakTimerClock:"00:00:00",
        interval:"",
        breakInterval:"",
        timerlistingByTrackId:[],
        selectedValues:[],
        getMessageAlert:{
            type:'',
            open:false,
            message:""
        },
        timerTags:[],
        dateFilterType:'Today',
        From:'',
        To:'',
        addManualResponse:'',
        tagResponseSave:'',
        tagsSaveForListing:'',
        expendListRow:true, //this state for timer listing expend
        clientsProject:[],
        pmProject:[],
        requestTimeResponse:'',
        commentResponse:'',
        addManualBreakResponse:'',
        commetListingState:[],
        timerTeamStatusState:[],
        projectWithTasksState:[],
        trackEntryFlagState:[],
        updateTrackEntryResState:''
    }),
    getters: {
        getTimer: (state) => {
            return state.timerInfo
        },
        getProjectDetails: (state) => {
            return state.timerProjectDetails
        },
        getProjectsByUser: (state) => {
            return state.projects
        },
        getTasksFromProject: (state) => {
            return state.tasks
        },
        getLoadingStatus: (state) => {
            return state.loading
        },
        getBreakTimeList: (state) => {
            return state.breakTimerList;
        },
        getTimerRunResponse: (state) => {
            return state.timerRunResponse
        },
        getTimerClock: (state) => {
            return state.timerClock
        },
        getUserInformation: () => {
            const { user } = useloggedUserInfo();
            return user.profile;
        },
        getTimerListing: (state) => {
            return state.timerListing
        },
        getTimerRunResponse:(state)=>{
            return state.timerRunResponse
        },
        getTimerClock:(state)=>{
            return state.timerClock
        },
        getBreakTimerClock:(state)=>{
            return state.breakTimerClock
        },
        getTimerListingFromTrackId:(state)=>{
            return state.timerlistingByTrackId
        },
        getMessage:(state)=>{
            return state.getMessageAlert
        },
        getTags:(state)=>{
            return state.timerTags
        },
        getAlertMessage:(state)=>{
            return state.getMessageAlert
        },
        getTagSaveRes:(state)=>{
            return state.tagResponseSave
        },
        tagsSaveForListingResponse:(state)=>{
            return state.tagsSaveForListing
        },
        getclientsProject:(state)=>{
            return state.clientsProject
        },
        getPmProject:(state)=>{
            return state.pmProject
        },
        getRequestTimeResponse:(state)=>{
            return state.requestTimeResponse
        },
        getCommentResponse:(state)=>{
            return state.commentResponse
        },
        getAddManualBreakResponse:(state)=>{
            return state.addManualBreakResponse
        },
        getCommentListing:(state)=>{
            return state.commetListingState
        },
        getTimerTeamStatusState:(state)=>{
            return state.timerTeamStatusState
        },
        getProjectWithTasks:(state)=>{
            return state.projectWithTasksState
        },
        getTrackEntryFlag:(state)=>{
            return state.trackEntryFlagState
        },
        getUpdateTrackEntryRes:(state)=>{
            return state.updateTrackEntryResState
        }

    },
    actions: {
        alertBOxForMessage(msg){
          this.getMessageAlert = msg
        },
        async submitProjectInfo(info) {
            var vm=this
            // let param = 'projectId='+info?.projectId+'&trackDescription='+info?.trackDescription+'&taskId='+info?.taskId+'&isBillable=0&startTime='+info.startTime+'&stopTime='+info.stopTime+'&currentDate='+info.currentDate+'&timeformat='+info.timeformat+'&type='+info.type;
            this.loading = true
            try {
                const { data } = await DataService.SaveProjectInfo(info);
                this.getMessageAlert = data
                this.timerRunResponse = data
                if(data.userMsg){
                    let message = data.userMsg 
                    if(data.userMsg=="stop")
                {
                ShowAlert(i18n.t('TimeEntryStopSuccessfully'), "success", true, i18n.t('Alert'));
                }
                    else if(data.userMsg=="TimerResumedAlready"){
                        ShowAlert(i18n.t('TimerResumedAlready'), "danger", true, i18n.t('Alert'));
                    }
                    else if(data.userMsg=="TimerStopped"){
                        ShowAlert(i18n.t('TimerStopped'), "danger", true, i18n.t('Alert'));
                    }
                    else if(data.userMsg=="TimerStartedAlready"){
                        ShowAlert(i18n.t('TimerStartedAlready'), "danger", true, i18n.t('Alert'));
                    }
                    else if(data.userMsg=="TimerPaused" && data.userMsg=="TimerPausedAlready"){
                        ShowAlert(i18n.t('TimerPausedAlready'), "danger", true, i18n.t('Alert'));
                    }
                    else{
                        ShowAlert(message, "danger", true, i18n.t('Alert'));
                    }

                }
            } catch (e) {
            console.log(e)
            }
            this.loading = false
        },
        async getProjects() {
            this.loading = true
            try {
               // ShowAlert("Valid  files are JPEG, PNG, GIF,BMP", "warning", true, "Alert");
                const { data } = await DataService.GetTimerProjectsByUserID();
                this.projects = data?.res
            } catch (e) {

            }
            this.loading = false
        },
        async loadTasks(id) {
            this.loading = true
            try {
                const { data } = await DataService.LoadTasks(id);
                this.tasks = data.map((item)=>{
                    if(item.parentValue > 0){
                    item.$isDisabled =true 
                    }
                    return item
                })
                //console.log("checkk",data?.res)
            } catch (e) {

            }
            this.loading = false
        },
        async getTimerInfo() {
            this.loading = true
            try {
                const { data } = await DataService.GetIncompleteEntry();
                this.timerInfo = data.result[0]
                this.timerProjectDetails = data.result[0]
            } catch (e) {
            }
            this.loading = false

        },
        async getBreakTime() {
            this.breakTimerList = []
            this.loading = true
            try {
                const { data } = await DataService.GetBreakTimeList();
                data?.result?.map((item) => {
                   
                    this.breakTimerList.push({
                        name:item.breakDetail,
                        value:item.breakId,
                        breakDuration:item.breakDuration,
                        isBillable:item.isBillable,
                        isPaid:item.isPaid
                    })
                })
            } catch (e) {

            }
            this.loading = false
        },
        async GetTimerListingByTrackId(id){
            this.loading = true
            try {
                
                const { data } = await DataService.GetTimerListingByTrackId(id);
                this.timerlistingByTrackId = data
            } catch (e) {
            }
             this.loading = false
        },
        async getTimerListing1(url) {
            this.loading = true
            try {
                const { data } = await DataService.GetTimerListing(url);
                //this.timerListing = []
               // setTimeout(()=>{
                    this.timerListing = data
               // },100)
                
            } catch (e) {
               
            } this.loading = false
        },
        setTimerClock(sec){
            this.timerClock= formatter(sec)
        },
        startTimer(){
            clearInterval(this.interval);
            clearInterval(this.breakInterval);
           this.interval = setInterval(() => {
            this.timerCountInSec++
            this.setTimerClock(this.timerCountInSec);
        }, 1000);
        },
        stopTimer(){
            clearInterval(this.interval);
        },
        breakSetTimerClock(sec){
            this.breakTimerClock= formatter(sec)
        },
        breakStartTimer(){
            clearInterval(this.breakInterval);
            clearInterval(this.interval);
           this.breakInterval = setInterval(() => {
                this.breakTimerCountInSec--
                this.breakSetTimerClock(this.breakTimerCountInSec,'positive')
        }, 1000);
        },
        breakStopTimer(){
            clearInterval(this.breakInterval);
        },
        //add manualy timer
    async addManualyTimer (timerData){
        var vm=this
        try{
            const {data }=await DataService.AddManually(timerData)
            this.getMessageAlert = data
            if(data.message){
                this.addManualResponse = data
                if(data.message=="Saved")
                    {
                    ShowAlert(i18n.t('TimeEntrySavedSuccessfully'), "success", true, i18n.t('Alert'));
                    }
                if(data.message=="overlap")
                    {
                    ShowAlert(i18n.t('OverlapEntry'), "success", true, i18n.t('Alert'));
                    }
                    else{
                        ShowAlert(data.message, "danger", true, i18n.t('Alert'));
                    }
            }


        }catch(e){
            console.log("data",e)
        }
    },
    async tagGetForTimer (){
        try{
            const {data }=await DataService.GetCompanyTaglist()
            this.timerTags = data.result

        }catch(e){
            console.log("data",e)
        }
    },
    async deleteChildTimer(body){
        var vm=this;
        try{
            const {data} =await DataService.DeleteByTrackId(body)
            if(data.msg="success"){
                ShowAlert(i18n.t('DeletedSuccessfully'), "Success", true, i18n.t('Alert'));
            }
            

        }catch(e){
            console.log(e)
        }
    },
    //Save tags 
    async tagsSave(record){
        try{
            const {data} =await DataService.SaveTagPopUp(record);
             this.tagResponseSave = data
            if(data=="added Successfully"){
                ShowAlert("Tags added Successfully", "Success", true, i18n.t('Alert'));
            }else{
                ShowAlert(data, "danger", true, i18n.t('Alert'));
            }
        }catch(e){

        }
    },
    async UpdateOrDeleteEntries(record){
        try{
            const {data} = await DataService.setAction(record)
             this.tagsSaveForListing = data
        }catch(e){
            ShowAlert(e, "danger", true, i18n.t('Alert'));
        }
    },
    async requestTime(param){
        try{
            const {data} = await DataService.RequestTime(param);
            this.requestTimeResponse =data
            if(data){
                
                if(data.status=="Success")
                {
                ShowAlert(data.status, "success", true, i18n.t('Alert'));
                }
                else if(data.status=="TimerResumedAlready"){
                    ShowAlert(i18n.t('TimerResumedAlready'), "danger", true, i18n.t('Alert'));
                }
                

            }
        }catch(e){
            ShowAlert(e, "danger", true, i18n.t('Alert'));
        }
    },
    async commetsFromClient(param){
        try{
            const {data} = await DataService.Comments(param);
            this.commentResponse =data.result.result
            console.log("check",data)
            if(data.result.result){
                
                if(data.result.result.status=="Success")
                {
                ShowAlert(i18n.t('CommentHasBeenPostedSuccessfully'), "success", true, i18n.t('Alert'));
                }
                else if(data.result.result.status=="TimerResumedAlready"){
                    ShowAlert(i18n.t('TimerResumedAlready'), "danger", true, i18n.t('Alert'));
                }
                
            }
        }catch(e){
            ShowAlert(e, "danger", true, i18n.t('Alert'));
        }
    },
    async pmGetFromProjectId(param){
        try{
            const {data} = await DataService.PMGetfromProject(param);
            
            this.pmProject = data.result.DATA
        }catch(e){
            ShowAlert(e, "danger", true, i18n.t('Alert'));
        }
    },
    async clientsGetFromProjectId(param){
        try{
            const {data } = await DataService.ClientsGetFromProject(param);
           this.clientsProject = data.result.DATA
        }catch(e){
            ShowAlert(e, "danger", true, i18n.t('Alert'));
        }
    },
    async addManualBreak(param){
        try{
            const { data } = await DataService.AddManuallyBreak(param)
            this.addManualBreakResponse=data
        }catch(e){
            ShowAlert(e, "danger", true, i18n.t('Alert'));
        }

    },
    async commetListing(param){
        this.loading = true
        try{
            const { data } = await DataService.CommentListing(param)
            this.commetListingState=data
        }catch(e){
            
                ShowAlert(e, "danger", true, i18n.t('Alert'));
           
        }
        this.loading = false

    },
    async TimerTeamStatus(param){
        this.loading = true
        try{
            const {data} = await DataService.TimerTeamStatus(param)
            this.timerTeamStatusState = data.result
        }catch(e){

        }
        this.loading = false
    },
    async projectWithTasks(){
        try{
            const { data } = await DataService.GetProjectWithTasks()
            this.projectWithTasksState=data.map((item)=>{
                item.tasks.map((record)=>{
                    record.projectName = record.taskName
                    record.projectId = record.taskId
                    return record
                })
                return item
            })
        }catch(e){
            
                //ShowAlert(e, "danger", true, i18n.t('Alert'));
           
        }

    },
    
    async trackEntryFlag(params){
        try{
            const { data } = await DataService.TrackEntryFlag(params)
            this.trackEntryFlagState=data
        }catch(e){
            
                //ShowAlert(e, "danger", true, i18n.t('Alert'));
           
        }

    },
    async updateTrackEntry(params){
        try{
            const { data } = await DataService.UpdateTimeTrackEntry(params)
            this.updateTrackEntryResState=data
        }catch(e){
            
                //ShowAlert(e, "danger", true, i18n.t('Alert'));
           
        }

    },
   
    
},
    // persist: true,
})